table{
  border: solid 1.4px #ddd;
  margin: .6rem;
  padding: .7rem;
}
table thead{
    background-color: #eee;
    text-align: center;
  }
table tbody tr:nth-child(2n){
    background-color: #eee
  }
table tbody td{
    border: dashed 1px #eef;
    padding: .8rem
}

@media screen and (min-width: 670px){
  #details{
    position: relative;
    top: 140px;
  }
  #details>div{
    width: 600px;
    margin: auto;
  }
  #details>div #content{
    width: 600px;
    margin: 10px auto;
  }
  #title{
    text-align: center;
  }
  #last-modified{
    text-align: center;
  }
  #right-warn{
    text-align: center;
    color: red;
    margin: 10px auto;
    border-bottom: solid 1px red;
  }
}
@media screen and (max-width: 670px){
  #title,#last-modified{
    margin: 0 10px;
  }
  #last-modified{
    color: #999
  }
  #vcomments{
    padding: 0 10px;
  }
  #tags{
    margin: 20px 10px
  }
  #right-warn{
    text-align: center;
    color: white;
    margin: 10px auto;
    width: 200px;
    background-color: red;
    border-radius: 3px;
  }
}