
@media screen and (min-width: 670px){
  header{
    position: absolute;
    height: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    left: 50%;
    transform: translate(-50%,16%);
  }
  .site-logo img{
    /*display: block;*/
    width: 150px;
    /*-webkit-box-reflect: below 0 linear-gradient(transparent, white);*/
    /*left: 50%;*/
    /*transform: translateX(-20%);*/
  }
  .navs{
    display: flex;
    justify-content: space-between;
    margin: 0 100px
  }
  .navs div{
    margin: 0 20px
  }
}
@media screen and (max-width: 670px){
  header{
    position: sticky;
    top:-10px;
    height: 100px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: white;
  }
  .site-logo img{
   width: 100px
  }
  .navs{
    display: flex;
    justify-content: space-between;
    margin: 0 40px
  }
  .navs div{
    margin: 0 10px
  }

}