.article-item{
  border: solid 1px white;
  padding: 6px;
  border-bottom: dashed 1px #1296db;
}
.article-item .flex-box{
  display: flex;
  justify-content: flex-start;
  margin: 0 0 10px 10px
}

.article-item:nth-child(1){
  border-top: solid 1px #1296db;
}
.article-item .title-date{
  margin: 10px 0 10px 10px
}
.article-item .title-date .last-modified{
  font-size: .7rem;
  color: #666
}
.article-item .tags{
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
  margin: 0 0 0 10px;
}
.img-container{
  
}
.img-container img.tumbnail{
  width: 100px;
  height: 70px;
  border-radius: 5px;
  object-fit: cover;
}

.tag-item{
  margin: 5px 4px
}
